.posts_wrapper {
    margin: 0.5em;
  }
    
  .new_post {
    display: flex;
    flex-direction: column;
    margin-bottom: 1em;
  }
  
  .new_post textarea {
    border: 1px solid #e1e1e1;
    border-radius: 0.25em;
    margin-bottom: 0.5em;
    padding: 0.5em;
  }
  
  .new_post button {
    background-color: #333;
    color: #ffff;
    border: 0;
    border-radius: 3px;
    padding: 0.5em;
    font-size: 14px;
    cursor: pointer;
  }

  