.user_info {
    background-color: #fff;
    padding: 10px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  
  .user_avatar {
    background-color: #777;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin-right: 20px;
  }

  .user_avatar img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }

  .status {
    color: #777
  }